// Robust score helpers for accuracy, tagging, and rough Elo-from-game estimation.

export type UciScore = { type: 'cp' | 'mate'; value: number };

// -------------------------------- Core score math --------------------------------

/** Normalize engine scores to centipawns (clipped), POV = mover. */
export function cpFromScore(score: UciScore | null | undefined): number | null {
  if (!score || typeof score.value !== 'number') return null;
  if (score.type === 'cp') {
    // Clamp to something sane; avoids outliers exploding averages
    return Math.max(-2000, Math.min(2000, score.value));
  }
  // Map mate distance to large cp; closer mates get larger magnitude.
  // Sign = positive if mate for mover, negative if getting mated.
  const sign = score.value >= 0 ? 1 : -1;
  const cp = 10000 - Math.min(99, Math.abs(score.value)) * 100; // mate in 1  9900
  return sign * cp;
}

/** Centipawn loss for a half-move: how much worse than best for the mover. */
export function cpLossForMove(
  best: UciScore | null | undefined,
  after: UciScore | null | undefined
): number | null {
  const b = cpFromScore(best);
  const a = cpFromScore(after);
  if (b == null || a == null) return null;
  // For the mover, higher is better. Loss = max(0, best - actual).
  return Math.max(0, b - a);
}

/** Aggregate average CPL per side; skips nulls/books. */
export function avgCplPerSide(halfMoves: Array<{
  side: 'W' | 'B';
  best?: UciScore | null;
  after?: UciScore | null;
}>): { avgW: number | null; avgB: number | null; nW: number; nB: number } {
  let sumW = 0, nW = 0, sumB = 0, nB = 0;
  for (const hm of halfMoves) {
    const loss = cpLossForMove(hm.best ?? null, hm.after ?? null);
    if (loss == null) continue;
    if (hm.side === 'W') { sumW += loss; nW++; }
    else { sumB += loss; nB++; }
  }
  return {
    avgW: nW ? sumW / nW : null,
    avgB: nB ? sumB / nB : null,
    nW, nB
  };
}

// ------------------------------- Accuracy (stricter) -------------------------------

/**
 * Accuracy from average CPL using a tunable curve (0..100).
 * Smaller K makes accuracy drop faster (stricter).
 * Default K=45 (stricter than your previous ~75).
 * floor trims optimistic tails on very small ACPL at shallow depth.
 */
export function accuracyFromAvgCpl(avgCpl: number | null, K = 45, floor = 5): number | null {
  if (avgCpl == null || !isFinite(avgCpl)) return null;
  const raw = 100 * (1 - avgCpl / (avgCpl + K)); // 100 * K / (avg + K)
  const clamped = Math.max(floor, Math.min(100, Math.round(raw)));
  return clamped;
}

/** Optional: CPL  qualitative tag aligned with stricter accuracy. */
export function tagFromLoss(lossCp: number): 'Best' | 'Good' | 'Mistake' | 'Blunder' {
  if (lossCp <= 15) return 'Best';
  if (lossCp <= 50) return 'Good';       // slightly tighter than before
  if (lossCp <= 120) return 'Mistake';
  return 'Blunder';
}

// ------------------------------- Elo from game (rough) ------------------------------

/**
 * Estimate an Elo from a single game using ACPL with optional penalties.
 * This is a heuristic and *not* a FIDE/Glicko ratingtune constants for your pool.
 *
 * Model:
 *   baseElo  A - B * ln(ACPL + C)
 *   penalties reduce Elo for high densities of mistakes/blunders per 40 half-moves.
 *
 * Defaults calibrate roughly as:
 *   ACPL ~ 20   ~2300
 *   ACPL ~ 60   ~17001800
 *   ACPL ~120   ~12501350
 */
export function estimateEloFromGame(params: {
  avgCpl: number | null;       // average centipawn loss for the player
  halfMoves?: number;          // number of half-moves played by the player (not total plies in game)
  mistakes?: number;           // count of moves tagged "Mistake"
  blunders?: number;           // count of moves tagged "Blunder"
  minMovesForConfidence?: number; // threshold to avoid noisy short games
  // tuning knobs:
  A?: number; B?: number; C?: number;          // curve constants
  blunderPenalty?: number; mistakePenalty?: number; // per-40-half-moves penalties
  minElo?: number; maxElo?: number;            // clamp
}): number | null {
  const {
    avgCpl,
    halfMoves = 0,
    mistakes = 0,
    blunders = 0,
    minMovesForConfidence = 16,
    A = 4681, B = 700, C = 10,
    blunderPenalty = 25, mistakePenalty = 10,
    minElo = 800, maxElo = 2800,
  } = params;

  if (avgCpl == null || !isFinite(avgCpl)) return null;
  // base Elo from ACPL (log curve)
  const base = A - B * Math.log(avgCpl + C);

  // density normalization: count per 40 half-moves
  const scale = halfMoves > 0 ? 40 / Math.max(20, halfMoves) : 1;
  const penalty =
    scale * (blunders * blunderPenalty + mistakes * mistakePenalty);

  const est = Math.round(Math.max(minElo, Math.min(maxElo, base - penalty)));

  // Low-confidence short games: still return a number, but caller can gray it.
  return est;
}

/**
 * Convenience: compute ACPL, accuracy, tags, and Elo for both sides from raw per-move data.
 * Expects an array of moves with stored best and after scores and side-to-move.
 */
export function summarizeGame(moves: Array<{
  side: 'W' | 'B';                        // mover
  best?: UciScore | null;                 // engine best score (POV mover)
  after?: UciScore | null;                // score after the played move (POV mover)
}>): {
  white: { avgCpl: number | null; accuracy: number | null; mistakes: number; blunders: number; halfMoves: number; estElo: number | null; };
  black: { avgCpl: number | null; accuracy: number | null; mistakes: number; blunders: number; halfMoves: number; estElo: number | null; };
} {
  // Build per-side arrays
  const whiteMoves: typeof moves = [];
  const blackMoves: typeof moves = [];
  for (const m of moves) (m.side === 'W' ? whiteMoves : blackMoves).push(m);

  // Compute losses + tags to count mistakes/blunders
  function sideStats(arr: typeof moves) {
    let sum = 0, n = 0, mistakes = 0, blunders = 0;
    for (const m of arr) {
      const loss = cpLossForMove(m.best, m.after);
      if (loss == null) continue;
      n++; sum += loss;
      const tag = tagFromLoss(loss);
      if (tag === 'Mistake') mistakes++;
      if (tag === 'Blunder') blunders++;
    }
    const avgCpl = n ? sum / n : null;
    const accuracy = accuracyFromAvgCpl(avgCpl);
    const estElo = estimateEloFromGame({
      avgCpl: avgCpl ?? null,
      halfMoves: n,
      mistakes,
      blunders,
    });
    return { avgCpl, accuracy, mistakes, blunders, halfMoves: n, estElo };
  }

  return {
    white: sideStats(whiteMoves),
    black: sideStats(blackMoves),
  };
}

